<template>
  <v-app>

    <v-app-bar 
  :elevation="4" 
  style="z-index: 1005; background: url('img/logos/menu-abr-bg.png.jpg') no-repeat center center;background-size: 100% auto; opacity: 90%;" 
  height="60px"
  class="px-0"
  fixed
>
  <!-- Left Navigation Icon -->
  <v-app-bar-nav-icon style="color:#4036ff;" @click="leftDrawer = !leftDrawer"></v-app-bar-nav-icon>
  
  <!-- Title and Logo -->
  <v-layout row align-center justify-center class="ma-0" style="flex: 1;">
    <h2 style="color: #948819; font-family: 'Roboto', sans-serif; font-size: 20px; font-weight: bold; text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5); margin: 0;">
      <!-- SILVER CITY -->
    </h2>
  </v-layout>
  
  <!-- Right Navigation Icon -->
  <v-app-bar-nav-icon style="color:#4036ff;" @click="rightDrawer = !rightDrawer"></v-app-bar-nav-icon>
</v-app-bar>




    <!-- Left Drawer -->
    <FiltersPanel v-model="leftDrawer" />

    <!-- Main Content -->
    <v-main>
      <v-card class="mt-4" height="89.4%">
        <v-toolbar flat dense>
          <!-- <v-app-bar-nav-icon style="color:#03396c" @click="leftDrawer = !leftDrawer"></v-app-bar-nav-icon>
          <v-spacer></v-spacer>
          
          <v-layout row align-center justify-space-between class="ma-0">
            <v-spacer></v-spacer>
            <img src="img/logos/Silver_City_Logo.png" height="80px" class="mx-0" />
            <h2 class="gradient-text">SILVER CITY</h2>
            <v-spacer></v-spacer>
          </v-layout>

          <v-spacer></v-spacer>
          <v-app-bar-nav-icon style="color:#03396c" @click="rightDrawer = !rightDrawer"></v-app-bar-nav-icon> -->
        </v-toolbar>

        <div class="gradient-bg" style="height: 100%">
          <v-divider></v-divider>
          <v-card-text style="height: 100%" class="pa-1">
            <GeoJsonMap />
          </v-card-text>
        </div>
      </v-card>
    </v-main>

    <!-- Right Drawer -->
    <VisualizationPanel v-model="rightDrawer" />

    <!-- Loading Overlay -->
    <v-overlay :opacity="0.8" :value="loading">
      <v-progress-circular indeterminate size="100">
        Loading...
      </v-progress-circular>
    </v-overlay>
  </v-app>
</template>


<script>
import { mapState, mapGetters } from 'vuex';
import L from "leaflet";
import chroma from "chroma-js";


import FiltersPanel from '@/components/filters/FiltersPanel.vue';
import GeoJsonMap from '@/components/GeoJsonMap.vue';
import VisualizationPanel from '@/components/visualizations/VisualizationPanel.vue';


import shp from "shpjs";
import Axios from "axios";
import JsonExcel from "vue-json-excel";

export default {
  components: {
    FiltersPanel,
    GeoJsonMap,
    downloadExcel: JsonExcel,
    VisualizationPanel
  },
  // props: {themeID: String, indicatorID: String, source: String, yeart: String},
  data() {
    return {

      showGeoJsonLayer: true,
      tableHeaders: [
        { text: 'Plot ID', value: 'plot_id' },
        { text: 'Dimensions', value: 'Dimensions' },
        { text: 'Zone', value: 'Zone' },
        { text: 'Owner', value: 'J_Owner' },
        { text: 'Status', value: 'SoldStatus' }
      ],

      // Add new filter selections
      selectedCategory: null,
      selectedZone: null,
      selectedDimension: null,
      selectedOwner: null,
      selectedStatus: null,
      // Counters for available options
      categoryCounter: 0,
      zoneCounter: 0,
      dimensionCounter: 0,
      ownerCounter: 0,
      statusCounter: 0,


      tempProv: [],
      firstloaded: true,
      stepper: 0,
      colortemp: [],
      ranger: [],
      mode: "",
      unit: "",
      totalValuesPro: [],
      totalValuesNat: [],
      proRadio: false,
      natRadio: false,
      DisagRadiosNat: null,
      xAxisNatSex: [],
      xAxisNatRes: [],
      tempTotal: false,
      tempSex: [],
      tempResi: [],
      DisagRadios: null,
      genderSr: [],
      residenceSr: [],
      xAxis: [],

      radioDistrict: false,
      radioDivision: false,
      radioProvince: false,
      showradiobuttons: false,
      radios: null,

      yearListDrop: [],
      selectedYear: "",
      sourceListDrop: [],
      selectedSource: "",
      loading: true,
      leftDrawer: true,
      rightDrawer: false,
      baseMaps: {
        dark: {
          url:
            "https://tiles.stadiamaps.com/tiles/alidade_smooth_dark/{z}/{x}/{y}{r}.png",
          maxZoom: 20,
        },
        gray: {
          url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
          maxZoom: 20,
        },
      },
      minZoom: 9,
      zoom: 15,
      center: [33.541393, 72.198702],
      bounds: null,
      shapefile: {
        loaded: false,
        geojson: null,
      },
      showNationaleShp: false,
      showProvinceShp: false,
      showDivisionShp: false,
      showDistrictShp: false,
      baseMap: "Grey",
      themecounter: 0,
      indiCounter: 0,
      sourceCounter: 0,
      yearCounter: 0,
      levelCounter: 0,
      provinceCounter: 0,
      divisionCounter: 0,
      districtCounter: 0,
      maxBounds: L.latLngBounds([
        [37.0841, 60.8729], // Northernmost and Westernmost point
        [23.6345, 77.8374]
      ]),
      themeList: [],
      themeId: "",
      indicatorsList: [],
      indicatorId: "",
      indicatorData: [],
      // districtData: [],
      year: "",
      dasy: [], // data after source and year filter
      districtData: [],
      divisionData: [],
      provinceData: [],
      nationalData: [],
      filterVals: [],
      selectedFilter: [],
      selectedDistrict: "",
      selectedDivision: "",
      selectedProvince: "",
      districtDrop: [],
      divisionDrop: [],
      provinceDrop: [],
      bottomsheet: false,
      showDisaggregation: true,
      search: "",
      tab: 0,
      ctab: 0,
      sr: [
        {
          name: "",
          data: [],
        },
      ],
      chartoptions: {
        legend: {
          showForSingleSeries: true,
          showForNullSeries: false,
          showForZeroSeries: true,
        },
        stroke: {
          curve: "smooth",
        },
        markers: {
          size: 5,
        },
        // chart: {
        //   id: "UNChart",
        //   height: 350,
        // },
        tooltip: {
          shared: true,
          intersect: false,
        },

        xaxis: {
          categories: [],
          axisBorder: {
            show: true,
            color: "#a24187",
            height: 1,
            width: "100%",
            offsetX: 0,
            offsetY: 0,
          },
          axisTicks: {
            show: true,
            borderType: "solid",
            color: "#a24187",
            height: 10,
            offsetX: 0,
            offsetY: 0,
          },
        },
        yaxis: {
          forceNiceScale: true,
          // min: 0,
          // max: 100,
          floating: false,

          axisBorder: {
            show: true,
            color: "#a24187",
            offsetX: 0,
            offsetY: 0,
          },
          axisTicks: {
            show: true,
            borderType: "solid",
            color: "#a24187",
            width: 6,
            offsetX: 0,
            offsetY: 0,
          },
          crosshairs: {
            show: true,
            position: "back",
            stroke: {
              color: "#b6b6b6",
              width: 1,
              dashArray: 0,
            },
          },
        },
        title: {
          text: undefined,
          align: "left",
          margin: 10,
          offsetX: 0,
          offsetY: 0,
          floating: false,
          style: {
            fontSize: "14px",
            fontWeight: "bold",
            fontFamily: undefined,
            color: "#263238",
          },
        },
        dataLabels: {
          enabled: false,
          offsetY: 100,
          style: {
            fontSize: "14px",
            colors: ["#696969"],
          },
        },
      },
      tempData: [],
      tempCategory: [],
      tableData: [],
    };
  },

  methods: {

     // Filter change handlers
     handleCategoryChange() {
      this.$store.dispatch('geojson/updateFilter', {
        filterType: 'category',
        value: this.selectedCategory
      });
    },

    handleZoneChange() {
      this.$store.dispatch('geojson/updateFilter', {
        filterType: 'zone',
        value: this.selectedZone
      });
    },

    handleDimensionChange() {
      this.$store.dispatch('geojson/updateFilter', {
        filterType: 'dimensions',
        value: this.selectedDimension
      });
    },

    handleOwnerChange() {
      this.$store.dispatch('geojson/updateFilter', {
        filterType: 'owner',
        value: this.selectedOwner
      });
    },

    handleStatusChange() {
      this.$store.dispatch('geojson/updateFilter', {
        filterType: 'soldStatus',
        value: this.selectedStatus
      });
    },

    styleGeoJson(feature) {
      const baseStyle = {
        weight: 1,
        opacity: 1,
        fillOpacity: 0.7,
        color: '#666'
      };

      const props = feature.properties;
      if (!props) return baseStyle;

      if (props.Category === 'Plot') {
        return {
          ...baseStyle,
          fillColor: this.getDimensionColor(props.Dimensions)
        };
      }
      return {
        ...baseStyle,
        fillColor: this.getCategoryColor(props.Category)
      };
    },

    getDimensionColor(dimension) {
      const colors = {
        '30 x 52.5': '#1E88E5',
        '38 x 71': '#43A047',
        '50 x 90': '#E53935',
        '40 x 60': '#FDD835',
        '25 x 45': '#8E24AA'
      };
      return colors[dimension] || '#757575';
    },

    getCategoryColor(category) {
      const colors = {
        'Community Center': '#FF7043',
        'Educational Complex': '#9C27B0',
        'Green/Open Space': '#4CAF50',
        'Mosque': '#2196F3',
        'Park/Playground': '#8BC34A',
        'Road': '#9E9E9E',
        'School': '#3F51B5'
      };
      return colors[category] || '#757575';
    },

    onEachGeoJsonFeature(feature, layer) {
      layer.on({
        mouseover: (e) => {
          const layer = e.target;
          layer.setStyle({
            weight: 2,
            fillOpacity: 0.9
          });
        },
        mouseout: (e) => {
          const layer = e.target;
          layer.setStyle(this.styleGeoJson(feature));
        }
      });

      if (feature.properties && feature.properties.Label) {
        layer.bindTooltip(feature.properties.Label, {
          permanent: false,
          sticky: true,
          direction: 'top'
        });
      }
    },

    genderChart() {
      this.sr = this.genderSr;
      this.chartoptions = {
        ...this.chartoptions,
        ...{
          xaxis: { categories: this.xAxis },
          // title: { text: this.filter.theme.name },
        },
      };
    },
    residenceChart() {

      this.sr = this.residenceSr;
      this.chartoptions = {
        ...this.chartoptions,
        ...{
          xaxis: { categories: this.xAxis },
          // title: { text: this.filter.theme.name },
        },
      };
    },
    totalChart() {

      // if (this.genderSr.lenth > 0) {
      this.sr = this.totalValuesPro;
      this.chartoptions = {
        ...this.chartoptions,
        ...{
          xaxis: { categories: this.xAxis },
          // title: { text: this.filter.theme.name },
        },
      };
      // } else this.updatechart();
    },
    genderChartNat() {
      this.sr = this.genderSr;
      this.chartoptions = {
        ...this.chartoptions,
        ...{
          xaxis: { categories: this.xAxisNatSex },
          // title: { text: this.filter.theme.name },
        },
      };
    },
    residenceChartNat() {
      this.sr = this.residenceSr;
      this.chartoptions = {
        ...this.chartoptions,
        ...{
          xaxis: { categories: this.xAxisNatRes },
          // title: { text: this.filter.theme.name },
        },
      };
    },
    totalChartNat() {
      // if (this.genderSr.lenth > 0) {
      this.sr = this.totalValuesNat;
      this.chartoptions = {
        ...this.chartoptions,
        ...{
          xaxis: { categories: this.xAxisNatRes },

        },
      };

    },

    getHeaders() {
      var sex = 0;
      var age = 0;
      var residence = 0;
      var district = 0;
      var division = 0;
      var province = 0;
      var sd1 = 0;
      var sd2 = 0;
      var sd3 = 0;
      var head = [];

      for (let index = 0; index < this.tableData.length; index++) {
        if (this.tableData[index].sex != "") {
          sex = 1;
        }
        if (this.tableData[index].residence != "") {
          residence = 1;
        }
        if (this.tableData[index].district != null) {
          district = 1;
        }
        if (this.tableData[index].division != null) {
          division = 1;
        }
        if (this.tableData[index].province != null) {
          province = 1;
        }
        if (this.tableData[index].age_group != "") {
          age = 1;
        }
        if (this.tableData[index].specific_description1 != "" && this.tableData[index].specific_description1 != null) {
          sd1 = 1;
        }
        if (this.tableData[index].specific_description2 != "" && this.tableData[index].specific_description2 != null) {
          sd2 = 1;
        }
        if (this.tableData[index].specific_description3 != "" && this.tableData[index].specific_description3 != null) {
          sd3 = 1;
        }
        // break;
      }
      head.push({ text: "Survey Level", value: "survey_level" });

      if (province == 1) {
        head.push({ text: "Province", value: "province" });
      }
      if (division == 1) {
        head.push({ text: "Division", value: "division" });
      }
      if (district == 1) {
        head.push({ text: "District", value: "district" });
      }
      if (residence == 1) {
        head.push({ text: "Residence", value: "residence" });
      }
      if (sex == 1) {
        head.push({ text: "Sex", value: "sex" });
      }

      if (age == 1) {
        head.push({ text: "Age", value: "age_group" });
      }
      if (sd1 == 1) {
        head.push(
          { text: "Disaggregation Title 1", value: "specific_name1" },
          { text: "Disaggregation Description 1", value: "specific_description1" }
        );
      }

      if (sd2 == 1) {
        head.push(
          { text: "Disaggregation Title 2", value: "specific_name2" },
          { text: "Disaggregation Description 2", value: "specific_description2" }
        );
      }
      if (sd3 == 1) {
        head.push(
          { text: "Disaggregation Title 3", value: "specific_name3" },
          { text: "Disaggregation Description 3", value: "specific_description3" }
        );
      }

      head.push({ text: "Value " + "(" + this.unit + ")", value: "current_value" });
      return head;

    },
    updatechart() {
      // this.criteria = "";
      this.tempCategory = [];
      this.tempData = [];
      this.sr = [
        {
          name: "",
          data: [],
        },
      ];
      this.chartoptions = {
        ...this.chartoptions,
        ...{
          xaxis: { categories: [] },
          title: { text: " " },
        },
      };
    },

    makeChartDistrict() {
      // var xaxis = [];
      this.tab = 0;
      var tempCategory = [];
      var tempData = [];

      for (let index = 0; index < this.districtData.length; index++) {
        // if ((this.districtData[index].specific_description1 == "" || this.districtData[index].specific_description1 == null) &&
        //   (this.districtData[index].specific_description2 == "" || this.districtData[index].specific_description2 == null) &&
        //   (this.districtData[index].specific_description3 == "" || this.districtData[index].specific_description3 == null))
        // {
        if (this.selectedFilter.length > 0) {
          if (this.selectedFilter.includes(this.districtData[index].district)) {
            tempData.push(this.districtData[index].current_value);
            tempCategory.push(this.districtData[index].district);
          }
        }
        else {
          tempData.push(this.districtData[index].current_value);
          tempCategory.push(this.districtData[index].district);
        }
        // }
      }
      this.sr = [
        {
          name: "Value",
          data: tempData,
        },
      ];

      this.chartoptions = {
        ...this.chartoptions,
        ...{
          xaxis: { categories: tempCategory },
          title: { text: " " },
        },
      };

    },
    makeChartDivision() {
      this.tab = 0;
      var tempCategory = [];
      var tempData = [];

      for (let index = 0; index < this.divisionData.length; index++) {

        if (this.selectedFilter.length > 0) {
          if (this.selectedFilter.includes(this.divisionData[index].division)) {
            tempData.push(this.divisionData[index].current_value);
            tempCategory.push(this.divisionData[index].division);
          }
        }
        else {
          tempData.push(this.divisionData[index].current_value);

          tempCategory.push(this.divisionData[index].division);
        }

      }

      this.sr = [
        {
          name: "Value",
          data: tempData,
        },
      ];
      this.chartoptions = {
        ...this.chartoptions,
        ...{
          xaxis: { categories: tempCategory },
          title: { text: " " },
        },
      };
      // window.dispatchEvent(new Event("resize"));
    },

    resetOnThemeChange() {
      this.tempProv = []
      this.tab = 0;
      this.showChart = true;
      this.totalValuesPro = [];
      this.totalValuesNat = [];
      this.tempSex = [];
      this.tempResi = [];
      this.tempTotal = false;
      this.DisagRadiosNat = null;
      this.proRadio = false;
      this.natRadio = false;

      this.DisagRadios = null;
      this.updatechart();
      this.radios = null;
      this.radioDistrict = false;
      this.radioDivision = false;
      this.radioProvince = false;

      this.indicatorsList = [];
      this.indiCounter = 0;
      this.yearListDrop = [];
      this.yearCounter = 0;
      this.districtData = [];
      this.districtCounter = 0;
      this.divisionData = [];
      this.divisionCounter = 0;
      this.provinceData = [];
      this.provinceCounter = 0;
      this.sourceListDrop = [];
      this.sourceCounter = 0;
      this.showradiobuttons = false;
    },
    resetOnIndicatorChange() {
      this.tempProv = []
      this.tab = 0;

      this.showChart = true;

      this.totalValuesPro = [];
      this.totalValuesNat = [];
      this.tempSex = [];
      this.tempResi = [];
      this.tempTotal = false;
      this.proRadio = false;
      this.natRadio = false;

      this.updatechart();
      this.DisagRadios = null;
      this.DisagRadiosNat = null;

      this.radios = null;
      this.radioDistrict = false;
      this.radioDivision = false;
      this.radioProvince = false;
      this.selectedSource = "";
      this.sourceListDrop = [];
      this.sourceCounter = 0;
      this.yearListDrop = [];
      this.yearCounter = 0;
      this.districtData = [];
      this.districtCounter = 0;
      this.divisionData = [];
      this.divisionCounter = 0;
      this.provinceData = [];
      this.provinceCounter = 0;
      this.showradiobuttons = false;
    },
    resetOnSourceChange() {
      this.tempProv = []
      this.tab = 0;

      this.showChart = true;

      this.totalValuesPro = [];
      this.totalValuesNat = [];
      this.tempSex = [];
      this.tempResi = [];
      this.tempTotal = false;
      this.proRadio = false;
      this.natRadio = false;

      this.updatechart();
      this.DisagRadios = null;
      this.DisagRadiosNat = null;

      this.radios = null;
      this.radioDistrict = false;
      this.radioDivision = false;
      this.radioProvince = false;

      this.yearListDrop = [];
      this.yearCounter = 0;
      this.districtData = [];
      this.districtCounter = 0;
      this.divisionData = [];
      this.divisionCounter = 0;
      this.provinceData = [];
      this.provinceCounter = 0;
      this.showradiobuttons = false;
    },
    resetOnYearChange() {
      this.tempProv = []
      this.tab = 0;

      this.showChart = true;

      this.totalValuesPro = [];
      this.totalValuesNat = [];
      this.tempSex = [];
      this.tempResi = [];
      this.tempTotal = false;
      this.proRadio = false;
      this.natRadio = false;

      this.updatechart();
      this.DisagRadios = null;
      this.DisagRadiosNat = null;

      this.radios = null;
      this.radioDistrict = false;
      this.radioDivision = false;
      this.radioProvince = false;

      this.districtData = [];
      this.districtCounter = 0;
      this.divisionData = [];
      this.divisionCounter = 0;
      this.provinceData = [];
      this.provinceCounter = 0;
    },

    getYear() {

      if (this.firstloaded) {
        this.firstloaded = false;
      }
      this.resetOnSourceChange();
      this.loading = true;
      var dataa = [];
      this.indicatorData.forEach((e) => {
        if (e.data_source_name == this.selectedSource) {
          dataa.push(e);
        }
      });
      this.dasy = dataa;
      this.unit = this.dasy[0].unit;


      this.latestYear(dataa);
      this.loading = false;
      this.resetChart()
    },

    latestYear(data) {
      // getting years and count
      let uniqueYear = [...new Set(data.map((item) => item.current_year))];
      this.yearListDrop = uniqueYear;
      this.yearCounter = 0;
      for (let i = 0; i < this.yearListDrop.length; i++) {
        if (this.yearListDrop[i]) this.yearCounter++;
      }
      // setting the highest year by default
      var yearsList = [];
      var yearCounter = 0;
      var bigYear = 0;
      for (let index = 0; index < data.length; index++) {
        yearsList.push(data[index].current_year);
      }
      for (let i = 0; i < yearsList.length; i++) {
        if (yearsList[i]) yearCounter++;
      }
      bigYear = yearsList[0];
      for (let index = 0; index < yearsList.length; index++) {
        if (bigYear < yearsList[index]) {
          bigYear = yearsList[index];
        }
      }
      this.selectedYear = bigYear;

      this.filterChild();
    },

    async filterChild() {
      this.selectedFilter = []
      this.showradiobuttons = true;
      this.resetOnYearChange();
      var tempData = this.dasy;
      var districtData = [];
      var divisionData = [];
      var provinceData = [];
      var nationalData = [];
      var tempDistrictDisaggr = [];
      this.ranger = [];
      this.stepper = 0;

      // getting national data
      for (let index = 0; index < tempData.length; index++) {
        if (tempData[index].survey_level == "National") {
          nationalData.push(tempData[index]);
        }
      }
      this.nationalData = nationalData;

      // getting district data
      for (let index = 0; index < tempData.length; index++) {
        if (tempData[index].district != null) {
          districtData.push(tempData[index]);
        }
      }
      if (districtData.length > 0) {
        for (let index = 0; index < districtData.length; index++) {
          if (
            (districtData[index].specific_description1 == "" || districtData[index].specific_description1 == null) &&
            (districtData[index].specific_description2 == "" || districtData[index].specific_description2 == null) &&
            (districtData[index].specific_description3 == "" || districtData[index].specific_description3 == null)
          ) {
            tempDistrictDisaggr.push(districtData[index]);
          }
        }

        let uniqueDistrict = [...new Set(districtData.map((item) => item.district))];
        this.districtDrop = uniqueDistrict;
        this.districtData = districtData;
      }
      //getting division data
      for (let index = 0; index < tempData.length; index++) {
        if (tempData[index].division != null) {
          divisionData.push(tempData[index]);
        }
      }
      if (divisionData.length > 0) {
        let uniqueDivision = [...new Set(divisionData.map((item) => item.division))];
        this.divisionDrop = uniqueDivision;
        this.divisionData = divisionData;
      }

      // getting province data
      for (let index = 0; index < tempData.length; index++) {
        if (tempData[index].province != null) {
          provinceData.push(tempData[index]);
        }
      }
      if (provinceData.length > 0) {
        let uniqueProvince = [...new Set(provinceData.map((item) => item.province))];
        this.provinceDrop = uniqueProvince;
        this.provinceData = provinceData;
      }

      // district counter
      for (let i = 0; i < districtData.length; i++) {
        if (districtData[i]) this.districtCounter++;
      }
      // division counter
      for (let i = 0; i < divisionData.length; i++) {
        if (divisionData[i]) this.divisionCounter++;
      }
      // province counter
      for (let i = 0; i < this.provinceData.length; i++) {
        if (this.provinceData[i]) this.provinceCounter++;
      }

      if (tempDistrictDisaggr.length != 0 && tempDistrictDisaggr.length < 30) {
        this.tableData = this.districtData;


        this.updatechart();
        await this.makeChartDistrict();

        this.districtshp();
        this.showDistrictShp = true;
        this.radioDistrict = true;
        this.radios = "district";
        // this.bottomsheet = true;
      }
      // else if (tempDistrictDisaggr.length == 0) {
      //   this.showChart = false;
      // }
      else if (this.divisionCounter > 0) {
        this.tableData = this.divisionData;
        setTimeout(() => {
          var lower = parseFloat(this.divisionData[0].current_value);
          var higher = parseFloat(this.divisionData[0].current_value);
          for (let index = 0; index < this.divisionData.length; index++) {
            if (this.divisionData[index].current_value < lower) {
              lower = parseFloat(this.divisionData[index].current_value);

            }
            if (this.divisionData[index].current_value > higher) {
              higher = parseFloat(this.divisionData[index].current_value);
            }
          }
          lower = Math.floor(lower);
          higher = Math.floor(higher);

          var step = Math.floor((higher - lower) / 5);
          this.stepper = step;
          if (lower < higher) {
            for (let index = lower; index <= higher; index += step) {
              this.ranger.push(index);
            }
          }
        }, 2000);

        this.divisionshp();
        this.showDivisionShp = true;

        this.updatechart();
        await this.makeChartDivision();
        this.filterVals = [...new Set(this.divisionData.map(item => item.division))];

        this.radioDivision = true;
        this.radios = "division";
        // this.bottomsheet = true;
      } else if (this.provinceCounter > 0) {
        this.tableData = this.provinceData;
        this.proDisagSex();

        setTimeout(() => {
          var lower = parseFloat(this.colortemp[0].current_value);
          var higher = parseFloat(this.colortemp[0].current_value);
          for (let index = 0; index < this.colortemp.length; index++) {
            if (this.colortemp[index].current_value < lower) {
              lower = parseFloat(this.colortemp[index].current_value);
            }
            if (this.colortemp[index].current_value > higher) {
              higher = parseFloat(this.colortemp[index].current_value);
            }
          }
          // lower = Math.floor(lower);
          // higher = Math.floor(higher);

          var step = (higher - lower) / 5;
          this.stepper = step;
          if (lower < higher) {
            for (let index = lower; index <= higher; index += step) {
              this.ranger.push(index);
            }
          }
        }, 2000);


        this.filterVals = [...new Set(this.provinceData.map(item => item.province))];
        this.radios = "province";
        this.provinceshp();
        this.showProvinceShp = true;

      } else if (nationalData.length > 0) {
        this.tab = 0;
        this.radios = "national";
        this.tableData = this.nationalData;
        this.proRadio = false;
        this.natRadio = false;
        await this.proDisagSexNat();
        this.loading = true;
        this.selectedDivision = "";
        this.selectedDistrict = "";

        this.showNationaleShp = false;
        this.showProvinceShp = false;
        this.showDistrictShp = false;
        await this.nationalshp();
        this.showNationaleShp = true;
        this.loading = false;
      }
