          (tempData[index].specific_description2 == "" || tempData[index].specific_description2 == null) &&
          (tempData[index].specific_description3 == "" || tempData[index].specific_description3 == null)

        ) {
          tempResi.push(tempData[index]);
        }
      }
      let xAxisNatRes = [...new Set(tempData.map((item) => item.residence))];


      for (let index = 0; index < tempResi.length; index++) {
        if (tempResi[index].residence == "Urban") {
          tempUrbanData.push(tempResi[index].current_value);
        }

        if (tempResi[index].residence == "Rural") {
          tempRuralData.push(tempResi[index].current_value);
        }
      }
      this.tempResi = tempResi;

      tempSrResi.push({
        name: "Total",
        data: tempTotal,
      });
      // }
      if (tempUrbanData.length > 0) {
        tempSrResi.push({
          name: "Urban",
          data: tempUrbanData,
        });
      }
      if (tempRuralData.length > 0) {
        tempSrResi.push({
          name: "Rural",
          data: tempRuralData,
        });
      }

      this.residenceSr = tempSrResi;
      this.xAxisNatRes = ["Residence"];

      if (tempSex.length > 0) {
        this.tempTotal = false;

        this.natRadio = true;
        this.genderChartNat();
        this.DisagRadiosNat = "gender";
      } else if (tempResi.length > 0) {
        this.natRadio = true;
        this.tempTotal = false;
        this.residenceChartNat();
        this.DisagRadiosNat = "residence";
      } else if (tempSex.length == 0 && tempResi.length == 0 && tempTotal.length > 0) {
        this.totalValuesPro = [];
        this.totalValuesNat = [];
        this.totalValuesNat.push({
          name: "Total",
          data: tempTotal,
        });
        this.natRadio = true;

        this.tempTotal = true;
        this.totalChartNat();
        this.DisagRadiosNat = "total";
      } else this.showChart = false;
    },

    provinceDetails() {
      console.log('in province details');
      this.tab = 0;
      this.radios = "province";
      if (this.tempProv.length == 0) {
        this.tempProv = this.provinceData
        this.filterVals = [...new Set(this.provinceData.map(item => item.province))]
      }
      if (this.tempProv.length > 0 && this.selectedFilter.length == 0) {
        this.provinceData = this.tempProv
      }
      else if (this.selectedFilter.length > 0) {
        this.provinceData = this.tempProv.filter(item => this.selectedFilter.includes(item.province));
      }


      this.tableData = this.provinceData;
      this.proDisagSex();
      this.proRadio = false;
      this.natRadio = false;

      // this.bottomsheet = true;
      this.loading = true;
      this.selectedDivision = "";
      this.selectedDistrict = "";
      this.showNationaleShp = false;
      this.showProvinceShp = false;
      this.showDistrictShp = false;
      this.provinceshp();
      this.showProvinceShp = true;
      this.loading = false;
    },
    async nationalDetails() {
      this.resetChart()
      console.log('in national details');
      this.tab = 0;
      this.radios = "national";

      this.tableData = this.nationalData;
      this.proRadio = false;
      this.natRadio = false;
      this.proDisagSexNat();
      // this.bottomsheet = true;
      this.loading = true;
      this.selectedDivision = "";
      this.selectedDistrict = "";

      this.showNationaleShp = false;
      this.showProvinceShp = false;
      this.showDistrictShp = false;
      await this.nationalshp();

      this.showNationaleShp = true;
      this.loading = false;
    },

    getColors(d, startColor = "#9ec2ff", endColor = "#03018c") {

      const scale = chroma.scale([startColor, endColor]).classes(this.ranger);


      return scale(d).hex();
    },

    async distDetails() {
      this.tab = 0;

      this.filterVals = [...new Set(this.districtData.map(item => item.district))];

      this.tableData = this.districtData;
      // this.bottomsheet = true;

      this.loading = true;
      this.radios = "district";
      this.selectedDivision = "";
      this.selectedProvince = "";

      this.showNationaleShp = false;
      this.showProvinceShp = false;
      this.showDivisionShp = false;
      await this.districtshp();
      this.showDistrictShp = true;
      // this.zoomProvince(district);
      this.updatechart();

      await this.makeChartDistrict();
      this.loading = false;
    },
    zoomProvince(nm) {
      var lat = "";
      var lng = "";
      for (let index = 0; index < this.shapefile.geojson.features.length; index++) {
        if (nm == this.shapefile.geojson.features[index].properties.NAME_1) {
          lat = this.shapefile.geojson.features[index].properties.lng;
          lng = this.shapefile.geojson.features[index].properties.lat;
          // this.styleSelected(this.shapefile.geojson.features[index])
        }
      }

      this.$refs.indiMap.mapObject.flyTo([lat, lng], 9);
    },
    zoomUpdated(zoom) {
      this.zoom = zoom;
    },
    centerUpdated(center) {
      this.center = center;

    },
    boundsUpdated(bounds) {
      this.bounds = bounds;
    },
    loadShapefile(path) {
      shp(path).then((geojson) => {

        this.shapefile.loaded = true;
        this.shapefile.geojson = geojson;
      });
    },
    getInidicatorList() {
      // this.Themename()
      this.resetChart()
      this.resetOnThemeChange();

      Axios.post(
        `https://ngdp-ncsw.org.pk/api/getIndicatorsList?theme_id=${this.themeId.id}`
      ).then((res) => {
        this.indicatorsList = res.data.quantitative_indicators;
        for (let i = 0; i < this.indicatorsList.length; i++) {
          if (this.indicatorsList[i]) this.indiCounter++;
        }
        if (this.firstloaded) {
          this.indicatorId = this.indicatorsList[1];
          this.getIndicatorData();
        }
        if (this.$route.params.themeID != undefined && this.mode == "fromLandingPage") {
          var inidcatorID = this.$route.params.indicatorID;
          this.setIndicator(inidcatorID);
          this.getIndicatorData();
        }
      });
    },
    async getIndicatorData() {

      this.loading = true;
      this.resetOnIndicatorChange();
      await Axios.post(
        `https://ngdp-ncsw.org.pk/api/getAllIndicatorsDetail?indicator_id=${this.indicatorId.id}`
      ).then((res) => {
        this.indicatorData = res.data.indicators;

        // getting unique source and count
        let uniqueSource = [
          ...new Set(this.indicatorData.map((item) => item.data_source_name)),
        ];
        this.sourceListDrop = uniqueSource;
        this.sourceCounter = 0;
        for (let i = 0; i < this.sourceListDrop.length; i++) {
          if (this.sourceListDrop[i]) this.sourceCounter++;
        }
        if (this.firstloaded) {
          this.selectedSource = this.sourceListDrop[0]
          this.getYear()
        }
        if (this.$route.params.themeID != undefined && this.mode == "fromLandingPage") {
          var source = this.$route.params.source;
          this.selectedSource = source;
          this.getYear();
          this.mode = "";
        }

        // this.filterByDefault();
        this.loading = false;
      });
      this.resetChart()
    },

    setTheme(id) {
      for (let index = 0; index < this.themeList.length; index++) {
        if (id == this.themeList[index].id) {
          var abc = this.themeList[index];
          this.themeId = abc;
        }
      }
    },
    setIndicator(id) {
      for (let index = 0; index < this.indicatorsList.length; index++) {
        if (id == this.indicatorsList[index].id) {
          var abc = this.indicatorsList[index];
          this.indicatorId = abc;
        }
      }
    },
    resetChart() {
      this.selectedFilter = []; // Clear the selectedFilter array

      if (this.radios === 'district') {
        this.makeChartDistrict();
      } else if (this.radios === 'division') {
        this.makeChartDivision();
      } else if (this.radios === 'province') {
        this.provinceDetails();
      }
    },
  },

  computed: {
    ...mapState('geojson', ['geojsonData']),
    ...mapGetters('geojson', [
      'filteredFeatures',
      'categoryOptions',
      'zoneOptions',
      'dimensionOptions',
      'ownerOptions',
      'soldStatusOptions'
    ]),
     // Computed properties for counters
     categoryList() {
      const options = this.categoryOptions || [];
      this.categoryCounter = options.length;
      console.log('Category List:', options, 'Counter:', this.categoryCounter);
      return options;
    },

    zoneList() {
      const options = this.zoneOptions || [];
      this.zoneCounter = options.length;
      console.log('Zone List:', options, 'Counter:', this.zoneCounter);
      return options;
    },

    dimensionList() {
      const options = this.dimensionOptions || [];
      this.dimensionCounter = options.length;
      console.log('Dimension List:', options, 'Counter:', this.dimensionCounter);
      return options;
    },

    ownerList() {
      const options = this.ownerOptions || [];
      this.ownerCounter = options.length;
      return options;
    },

    statusList() {
      const options = this.soldStatusOptions || [];
      this.statusCounter = options.length;
      return options;
    },
    // Computed properties for options with counts
    statusOptions() {
      const options = this.soldStatusOptions || [];
      this.statusCounter = options.length;
      return options;
    }
  },


  watch: {
    geojsonData: {
          immediate: true,
          handler(newData) {
              console.log('GeoJSON Data Changed:', newData ? 'Data Present' : 'No Data');
          }
      },
    // Watch getters for counter updates
    categoryOptions: {
      handler(newVal) {
        this.categoryCounter = newVal.length;
      },
      immediate: true
    },
    zoneOptions: {
      handler(newVal) {
        this.zoneCounter = newVal.length;
      },
      immediate: true
    },
    dimensionOptions: {
      handler(newVal) {
        this.dimensionCounter = newVal.length;
      },
      immediate: true
    },
    ownerOptions: {
      handler(newVal) {
        this.ownerCounter = newVal.length;
      },
      immediate: true
    }
  },

  // computed: {
  //   radioShow() {
  //     if (this.districtCounter > 0) {
  //       return (this.radioDistrict = true);
  //     }
  //     if (this.divisionCounter > 0) {
  //       return (this.radioDivision = true);
  //     }
  //     if (this.provinceCounter > 0) {
  //       return (this.radioProvince = true);
  //     }
  //   },
  // },

  mounted() {
    this.rightDrawer = true
    this.nationalshp();
    this.showNationaleShp = true;
    this.loading = false;
    this.$store.dispatch('geojson/loadGeoJSONData');

    if (this.$route.params.themeID != undefined) {
      this.mode = "fromLandingPage";
      var themeID = this.$route.params.themeID;
      // var inidcatorID = this.$route.params.inidcatorID
      // var source = this.$route.params.source
      // var year = this.$route.params.year

      setTimeout(() => {
        this.setTheme(themeID);
        this.getInidicatorList();
      }, 2000);
    }
  },
  async beforeMount() {
    await Axios.post(`https://ngdp-ncsw.org.pk/api/getThemes`).then(
      (res) => {
        this.themeList = res.data.themes;
        this.themecounter = 0;
        for (let i = 0; i < this.themeList.length; i++) {
          if (this.themeList[i]) this.themecounter++;
        }
        if (this.firstloaded) {

          this.themeId = this.themeList[0];
          this.getInidicatorList()
        }

      }
    );
  },
};
</script>

