.leaflet-tooltip.my-labels {
  color: white;
  background-color: transparent;
  border: transparent;
  box-shadow: none !important;
  font-size: 10px;
  width: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
}

.inline {
  display: inline;
}
.v-toolbar {
  border-bottom: 1px solid rgba(102, 126, 234, 0.1);
}
</style>
